import * as React from 'react'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'

export const TitelLanding = () => {
  return (
    <Typography variant='h2'>
      <Box
        sx={{
          fontWeight: 900,
          width: 737,
          letterSpacing: -3,
          fontSize: 48,
          color: 'black',
        }}
      >
        SOFTWARE DEVELOPMENT LIFE CYCLE
      </Box>
    </Typography>
  )
}
